@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.teamWrapper {
    margin: 4px;
    padding: 4px;
    display: flex;
    justify-content: flex-start;
    border: 2px solid var(--alt-bg);
}
/* 
.teamWrapper:nth-child(2n) {
    margin-bottom: 30px;
} */

.PlayoffTree .matchup:nth-child(2n) {
    margin-bottom: 30px;
}

.teamLogo {
    max-height: 40px;
    margin: auto;
}

.seed {
    background-color: var(--alt-bg);
    border: 2px solid black;
    border-radius: 100%;
    text-align: center;
    margin-top: 5px;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    color: var(--alt-text);
}



.PlayoffTreeWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    margin-left: 90px;
    padding: 15px;
    background-color: var(--main-bg);
    color: var(--main-text);
    border: 8px solid var(--alt-bg);
}

.PlayoffTreeWrapper section {
    text-align: left;
    display: flex;
    flex-direction: row;
    width: 96%;
    margin: 4px 2%;
}

.ByeRound {
    justify-content: space-around;
}

.ByeRound ul li {
    margin-bottom: 0;
}

section.PlayoffTree {
    justify-content: space-between;
    margin: 30px 15px;
}

ul li.west, ul li.east {
    width: 91.5px;
    height: 40px;
    margin: 4px;
    padding: 4px;
    display: flex;
    justify-content: flex-start;
    border: 2px solid var(--alt-bg);
    background-color: floralwhite;
}

.watermark {
    max-height: 100%;
    width: 100%;
    opacity: 0.4;
    text-align: center;
    font-size: 2.3rem;
    font-weight: bold;
    margin: 0;
}

.App {
  text-align: center;
  font-family: 'Karla', sans-serif;
  /* font-family: 'Rubik', sans-serif; */
}

:root {
  --main-bg: #ffffff;
  --alt-bg: #637282;
  --main-text:  #1b2733;
  --alt-text:  #ffffff;
  --button: #ab7124;
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.logo {
  height: 80px;
  pointer-events: none;
  position: absolute;
  left: 15px;
  top: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .Nhl-logo {
    -webkit-animation: nhl-logo-spin infinite 60s linear;
            animation: nhl-logo-spin infinite 60s linear;
  }
}

.App-header {
  background-color: #637282;
  background-color: var(--alt-bg);
  position: absolute;
  height: 100px;
  width: 100px;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes nhl-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  45% {
    transform: rotate(5deg);
  }
  46% {
    transform: rotate(175deg);
  }
  54% {
    transform: rotate(185deg);
  }
  55% {
    transform: rotate(355deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

